<template>
    <div id="header">
        <ul>
            <router-link tag="li" :to="{name: 'ShowBlog'}" exact>
                <a href="">我的博客</a>
            </router-link>
            <router-link tag="li" :to="{name: 'AddBlog'}">
                <a href="">添加博客</a>
            </router-link>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'my-header'
}
</script>

<style scoped>
ul {
    background: red;
    height: 60px;
    line-height: 60px;
    text-align: center;
}
li {
    display: inline-block;
    width: 100px;
    font-size: 20px;
}
a {
    color: #fff;
    padding: 10px;
}
.router-link-active {
    background: #ccc;
}
</style>




