<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required>

        <label>博客内容</label>
        <textarea v-model="blog.content" name="" id="" cols="30" rows="10"></textarea>

        <div id="checkboxes">
            <label>Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label>Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label>React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label>Ang.js</label>
            <input type="checkbox" value="Ang.js" v-model="blog.categories">
        </div>
        <label>作者</label>
        <select name="" id="" v-model="blog.author">
            <option v-for="(author, index) in authors" :key="index">{{author}}</option>
        </select>
        <button @click.prevent="post">添加博客</button>
    </form>
    <div v-if="submmited">
        <h3>您的博客已经发布成功</h3>
    </div>
    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容</p>
        <p>{{blog.content}}</p>
        <p>博客分类：</p>
        <ul>
            <li v-for="(categorie, index) in blog.categories" :key="index">
                {{categorie}}
            </li>
        </ul>
        <p>作者</p>
        <p>{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
        blog: {
            title: '',
            content: '',
            categories: [],
            author: ''
        },
        authors: [
            'James',
            'Gray',
            'Bucky'
        ],
        submmited: false
    }
  },
  methods: {
      post() {
        //   this.$http.post('https://jsonplaceholder.typicode.com/posts', {
            this.$http.post('https://vue-blog-demo-b629d.firebaseio.com/blogdata.json', {
                title: this.blog.title,
                body: this.blog.content
            })
            .then((data) => {
                if(data) {
                    this.submmited = true;
                }
            })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #add-blog * {
        box-sizing: border-box;
    }
    #add-blog {
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label {
        display: block;
        margin: 20px 0 auto;
    }
    input[type="text"],
    textarea,
    select {
        display: block;
        padding: 8px;
        width: 100%;
    }
    textarea {
        height: 200px;
    }
    #checkboxes label {
        display: inline-block;
        margin-top: 0;
    }
    #checkboxes input {
        display: inline-block;
        margin-right: 10px;
    }
    button {
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: none;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer;
    }
    #preview {
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    h3 {
        margin-top: 10px;
    }
</style>



