import Vue from 'vue'
import Router from 'vue-router'
import ShowBlog from '../components/ShowBlog'
import AddBlog from '../components/AddBlog'
import Blog from '../components/Blog'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ShowBlog',
      component: ShowBlog
    },
    {
      path: '/addblog',
      name: "AddBlog",
      component: AddBlog
    },
    {
      path: '/blog/:id',
      name: 'blog',
      component: Blog
    }
  ],
  mode: 'history'
})



