<template>
  <div v-theme:column="'narrow'" id="show-blog">
    <h1>博客总览</h1>
    <input type="text" placeholder="请输入搜索内容" v-model="search"> 
    <div v-for="(blog, index) in filteredBlogs" :key="index" class="single-blog">
        <router-link :to="'/blog/' + blog.id">
            <h2 v-rainbow>{{blog.title | toUppercase}}</h2>
        </router-link>
        <article>
            {{blog.body | snippet}}
        </article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blog',
  data () {
    return {
      blogs: [],
      search: ''
    }
  },
  created(){
    //   this.$http.get('https://jsonplaceholder.typicode.com/posts')
      this.$http.get('https://vue-blog-demo-b629d.firebaseio.com/blogdata.json')
                .then(data => {
                    return data.json();
                    // this.blogs = data.body.slice(0, 10);
                })
                .then(data => {
                    let blogArr = [];
                    for(let key in data) {
                        data[key].id = key;
                        blogArr.unshift(data[key])
                    }
                    this.blogs = blogArr;
                })
  },
  computed: {
      filteredBlogs() {
          return this.blogs.filter((blog) => {
              return blog.title.match(this.search)
          })
      }
  },
  filters: {
      toUppercase(value) {
          return value.toUpperCase();
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blog {
    max-width: 800px;
    margin: 0 auto;
}
.single-blog {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
}
input[type="text"] {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}
</style>



