<template>
    <div id="blog">
        <h2>{{blogData.title}}</h2>
        <article>{{blogData.body}}</article>
    </div>
</template>
<script>
export default {
    name: 'blog',
    data(){
        return {
            id: this.$route.params.id,
            blogData: {}
        }
    },
    created(){
        this.$http.get('https://vue-blog-demo-b629d.firebaseio.com/blogdata/' + this.id + '.json')
                  .then(data => {
                      this.blogData = data.body;
                  })
    }
}
</script>




